body {
  margin: 0;
  padding: 0;
  font-family: "Inter", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F7FAFF;
  color: #2E353F;

}

.todo-item-outer-container {
  background-color: white;
  color: #3A3131;
  display: block;
  padding: 1.5em 0.8em;
  justify-content: space-between;
  min-height: 27px;
  margin-bottom: 1em;
}

.todo-item-inner-container {
  background-color: white;
  color: #3A3131;
  display:flex;
  justify-content: space-between;
  min-height: 27px;
  -webkit-transition: all 0.2s ease-out 0s;
  transition: all 0.2s ease-out 0s;
  margin-bottom: 1em;
}

.todo-item-container-left {
  display: flex;
}

.todo-item-complete-button {
  border: 0;
  background: none;
  cursor: pointer;
  margin-right: 2px;
}

/* bad for a11y but looks better ðŸ¤·â€ */
/* good fix: https://medium.freecodecamp.org/how-to-make-your-fancy-svg-button-accessible-83c9172c3c15 */
.todo-item-complete-button:focus {
  outline: 0;
}

.todo-item-circle {
  border-radius: 100%;
  width: 24px;
  height: 24px;
}

.todo-item-circle-empty {
  box-sizing: border-box;
  border: 2px solid #A2ADC1;
}

.todo-item-circle-empty:hover {
  background: #a2adc12e;
}

.todo-item-circle-check {
  background: #47D80C;
  color: white;

  /*
    center children vertically and horizontally
    https://stackoverflow.com/questions/25832340/css-flexbox-vertically-horizontally-center-image-without-explicitely-defining-pa
  */
  display: flex;
  justify-content: center; /* align horizontal */
  align-items: center; /* align vertical */
}

.todo-item-circle-check > svg {
  width: 15px;
  height: 15px;
}

.todo-item-description {
  margin-bottom: 8px;
}

.todo-item-start-session-button {
  color: #A2ADC1;
  border: 0;
  background: none;
  cursor: pointer;
  display: none;
}

.todo-item-start-session-button > svg {
  width: 24px;
  height: 24px;
}

.todo-item-start-session-button:hover > svg {
  width: 25px;
  height: 25px;
}

/* bad for a11y but looks better ðŸ¤·â€ */
/* good fix: https://medium.freecodecamp.org/how-to-make-your-fancy-svg-button-accessible-83c9172c3c15 */
.todo-item-start-session-button:focus {
  outline: 0;
}

.todo-item-inner-container:hover .todo-item-start-session-button {
  display: block;
}

.break {
  background-color: #E8F5E9;
  /* background-image: linear-gradient(-180deg, #5EEDAA 3%, #50c78f 100%); */
}

.in-progress {
  background-color: #E3F2FD;
}
.sessions-completed-container {
  display:flex;
  flex-direction:row;
}

.sessions-line {
  width: 15px;
  height: 5px;
  border-radius: 1px;
  margin-left: 3px;
}

.completed {
  background: #77BFA3;
}

.remaining {
  background: #D6D6D6;
}

.exceeded {
  background: #C96480;
}
.timer-container {
  font-weight: 700;
  font-size: 2rem;
  color: #A2ADC1;
  padding: 1em 1em;
  display: flex;
  text-align: center;
  -webkit-transition: all 0.2s ease-out 0s;
  transition: all 0.2s ease-out 0s;
}

.timer-container > div {
  flex-grow: 1;
  width: 33%;
}

.timer-button {
  border: 0;
  background: none;
  color: #A2ADC1;
  cursor: pointer;
}

.timer-button > svg {
  width: 24px;
  height: 24px;
}

.timer-button > svg:hover {
  width: 25px;
  height: 25px;
}

/* bad for a11y but looks better ðŸ¤·â€ */
/* good fix: https://medium.freecodecamp.org/how-to-make-your-fancy-svg-button-accessible-83c9172c3c15 */
.timer-button:focus {
  outline: 0;
}

.todo-input-container > form {
  display: flex;
}

.todo-input, .todo-input-button {
  box-sizing: border-box;
  padding-top: 0.7em;
  padding-bottom: 0.7em;
  font-size: 1.2em;
  font-weight: 600;
  border: 0;
}

.todo-input {
  padding-left: 1em;
  background: #E7EDF8;
  border-radius: 4px 0 0 4px;
  flex-grow: 1;
}
.todo-input::-webkit-input-placeholder {
  color: #8A98AF;
}
.todo-input::-moz-placeholder {
  color: #8A98AF;
}
.todo-input::-ms-input-placeholder {
  color: #8A98AF;
}
.todo-input::placeholder {
  color: #8A98AF;
}

.addPomo-input {
  /* padding-left: 1em; */
  background: white;
  /* border-radius: 4px 0 0 4px; */
  /* flex-grow: 1; */
}

.todo-input-button {
  background: #4A6ED2;
  color: white;
  border-radius: 0 4px 4px 0;
  /*
  center children vertically and horizontally
  https://stackoverflow.com/questions/25832340/css-flexbox-vertically-horizontally-center-image-without-explicitely-defining-pa
  */
  display: flex;
  justify-content: center; /* align horizontal */
  align-items: center; /* align vertical */
  /* minimum size */
  flex-basis: 2.7em;
  cursor: pointer;
}

.todo-input-button > svg {
  width: 1em;
  height: 1em;
}

.flex-wrapper {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 1.1em;
}

header {
  margin-bottom: 0.9em;
  display: flex;
}

.heading {
  font-size: 2em;
  font-weight: 800;
  flex-grow: 70;
  background-color: #F4F9FF;
  border-width: 0px;
}

.clear-button {
  color: grey;
  border: 0;
  background: none;
  cursor: pointer;
  margin-right: 0.2em;
  /*
  center children vertically and horizontally
  https://stackoverflow.com/questions/25832340/css-flexbox-vertically-horizontally-center-image-without-explicitely-defining-pa
  */
  display: flex;
  justify-content: center; /* align horizontal */
  align-items: center; /* align vertical */

}

/* bad for a11y but looks better ðŸ¤·â€ */
/* good fix: https://medium.freecodecamp.org/how-to-make-your-fancy-svg-button-accessible-83c9172c3c15 */
.clear-button:focus {
  outline: 0;
}

.clear-button:hover > svg {
  width: 31px;
  height: 31px;
}

.clear-button > svg {
  width: 30px;
  height: 30px;
}

.card {
  border-radius: 9px;
  box-shadow: rgba(0, 0, 0, 0.11) 0px 4px 8px;
  -webkit-transition: all 0.2s ease-out 0s;
  transition: all 0.2s ease-out 0s;
  margin-bottom: 1em;
}

.card:hover {
  box-shadow: rgba(0, 0, 0, 0.13) 0px 6px 20px;
}

.empty-state-container {
  display: flex;
  flex-direction: column;
}

.empty-state-container > svg {
  margin: auto;
  flex-basis: 20em;
}

.empty-state-text {
  text-align: center;
  font-size: 1.2em;
}

.modal-outer-container {
  background-color: white;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  height: 14em;
  width: 33em;
  margin: 15% auto;
}

.modal-inner-container {
  height: 90%;
  width: 90%;
  margin: 7% auto;
  display:flex;
  flex-direction: column;
}

.modal-heading {
  color: #8A98AF;
}

.acknowledgements {
  color: #8A98AF;
  font-size:12px;
  margin-bottom: 0px;
  text-align: center;
}

